<template>
   <div id="showBlogs" v-theme:column="'narrow'">
      <h1>All blog articles:</h1>
      <input type="text" v-model="search" placeholder="Search blogs"/>
      <div class="single-blog" v-for="blog in filteredBlogs">
         <router-link v-bind:to="'/blog/' + blog.id"><h2>{{blog.title | toUppercase}}</h2></router-link>
         <article class="">
            {{blog.content | snippet }}
         </article>
      </div>
   </div>
</template>

<script>
import searchMixin from '../mixins/searchMixin';
export default {
  data () {
    return {
      blogs: [],
      search: '',
    }
},
methods: {
},
computed: {
},
created(){
   this.$http.get('https://vue-playlist-5e466.firebaseio.com/posts.json').then(function(data){
      return data.json();
   }).then(function(data){
      var blogsArray = [];
      for(let key in data){
         data[key].id = key;
         blogsArray.push(data[key]);
         console.log(blogsArray);
      }
      this.blogs = blogsArray;
   });
},
filters: {
   toUppercase(value){
      return value.toUpperCase();
   },
   snippet(value){
      return value.slice(0,100) + '...';
   }
},
directives: {
   'rainbow': {
      bind(el, binding, vnode) {
         el.style.color = "#" + Math.random().toString().slice(2,8);
      }
   }
},
mixins: [searchMixin]
}
</script>
<style>
#showBlogs{
    max-width: 800px;
    margin: 0px auto;
}
.single-blog{
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
}
</style>



