<template lang="html">
   <div id="singleBlog">
      <h1>{{blog.title}}</h1>
      <article>
         <p>{{blog.content}}</p>
      </article>
      <p>{{blog.author}}</p>
      <ul>
         <li v-for="category in blog.categories">{{category}}</li>
      </ul>
   </div>

</template>

<script>
export default {
   data() {
      return {
         id: this.$route.params.id,
         blog: {}
      }
   },
   created() {
      this.$http.get('https://vue-playlist-5e466.firebaseio.com/posts/' + this.id + '.json').then(function(data){
         return data.json();
      }).then(function(data){
         this.blog = data;
      });
   }
}
</script>

<style lang="css">
#singleBlog{
   max-width: 960px;
   margin: 0 auto;
}
</style>



